@mixin display
    display: flex
    align-items: center
    justify-content: space-between

@mixin desktop-low
    @media (max-width: 1728px)
        @content

@mixin low-resolution
    @media (max-width: 1602px)
        @content

@mixin low-resolution-gap
    @media (max-width: 1535px)
        @content

@mixin tablet
    @media (max-width: 1196px)
        @content

@mixin mobile
    @media (max-width: 950px)
        @content

@mixin mobile-phone
    @media (max-width: 650px)
        @content

@mixin mobile-iphone
    @media (max-width: 415px)
        @content

@mixin mobile-low
    @media (max-width: 400px)
        @content

@mixin mobile-iphone-low
    @media (max-width: 375px)
        @content

@mixin mobile-phone-low
    @media (max-width: 320px)
        @content
