.review
    height: 60vh
    text-align: center
    margin: 5rem 10rem 5rem 10rem

    @include low-resolution-gap
        margin: 5rem 8rem 5rem 8rem

    @include tablet
        height: 120vh

    @include mobile
        height: 160vh

    @include mobile-iphone
        height: 145vh

    @include mobile-low
        height: 150vh

    @include mobile-iphone-low
        height: 190vh

    @include mobile-phone-low
        height: 220vh

    .review-user
        margin-top: 5rem
        @include display

        @include low-resolution-gap
            gap: 5rem

        @include tablet
            flex-direction: column
